import React, {Component} from "react";
import Form from "./Components/Form/Form";
import ContactsList from "./Components/ContactsList/ContactsList";
import Filter from "./Components/Filter/Filter";
import { v4 as uuidv4 } from 'uuid';

class App extends Component {
  state = {
  contacts: [
    {id: 'id-1', name: 'Rosie Simpson', number: '459-12-56'},
    {id: 'id-2', name: 'Hermione Kline', number: '443-89-12'},
    {id: 'id-3', name: 'Eden Clements', number: '645-17-79'},
    {id: 'id-4', name: 'Annie Copeland', number: '227-91-26'},
  ],
  filter: '',
  name: '',
  number: ''
}

  handlerSubmit = event => {
    event.preventDefault();

    const contact = {
      id: uuidv4(),
      name: this.state.name,
      number: this.state.number
    }

    this.setState(({ contacts }) => ({
      contacts: [...contacts, contact],
    }))    
  }

  handleChange = event => {      
    this.setState({
      [event.currentTarget.name]: event.currentTarget.value
    })
  }

  handleFilter = (event) => {
    const { filter } = this.state;   
    const normalizedFilter = event.currentTarget.value.toLowerCase();
    this.setState({ filter: normalizedFilter })

    return (      
    this.setState(prevState => ({
      contacts: prevState.contacts.filter(contact => contact.name.toLowerCase().includes(filter)),
    })))     
  }

  handleDelete = (id) => {
    this.setState(prevState => ({
      contacts: prevState.contacts.filter(contact => contact.id !== id),
    }));
  };

  render() {
    return (
    <>
      <h1>Phonebook</h1>
      <Form handlerSubmit={this.handlerSubmit} handleChange={this.handleChange}/>
      <h2>Contacts</h2>
      <Filter handleFilter={this.handleFilter}/>
      <ContactsList contacts={this.state.contacts} handleDelete={this.handleDelete}/>
    </>  
  );
}}

export default App;