import styles from "./Filter.module.css";

const Filter = ({ handleFilter }) => {

  return (
      <label className={styles.filter_text}> Find contacts by name <br/>
        <input
        className={styles.filter_input}
        type="text"
        name="filter"        
        onChange={handleFilter}        
        autoComplete="off"/>
    </label>
  )
}

export default Filter